<template>
  <div class="setOtherTag">
    <!-- <van-overlay :show="showOverlay"/> -->
    <!-- 当一个字段有三个或者三个以上的值 请复制这个!!!!!!!!!!! -->
    <div class="btn">
      <span v-if="msggg.user_authen == 0" @click="setOtherTag(['user_authen',1])">设为-认证</span>
      <span v-else-if="msggg.user_authen == 1" @click="setOtherTag(['user_authen',0])">取消-认证</span>
      <span v-else @click="setOtherTag(['user_authen',1])">设为-认证</span>
    </div>

    <div class="btn">
      <span v-if="msggg.user_authen == 0" @click="setOtherTag(['user_authen',2])">设为-超级认证</span>
      <span v-else-if="msggg.user_authen == 2" @click="setOtherTag(['user_authen',0])">取消-超级认证</span>
      <span v-else @click="setOtherTag(['user_authen',2])">设为-超级认证</span>
    </div>

<!-- 当一个字段只有两个值 请复制这个!!!!!!!!!!! -->
	<div class="btn">
	  <span v-if="msggg.user_beauty == 0" @click="setOtherTag(['user_beauty',1])">设为-美少女</span>
	  <span v-else-if="msggg.user_beauty == 1" @click="setOtherTag(['user_beauty',0])">取消-美少女</span>
	  <!-- <span v-else @click="setOtherTag(['user_beauty',1])">设为-认证</span> -->
	</div>


  </div>
</template>

<script>


  import {createNamespacedHelpers} from 'vuex'
  const login=createNamespacedHelpers('login')
  // const navBar_Every=createNamespacedHelpers('navBar_Every')
  export default{
    name:"",
    /* 数据*/
    data(){
      return {
        // showOverlay : false
      }
    },
    props: {
        msggg: {
            type: Object, //指定传入的类型
            //type 也可以是一个自定义构造器函数，使用 instanceof 检测。
            default: {} //这样可以指定默认的值
        }
    },
    /* 方法*/
    methods:{
      setOtherTag(arg){
        if(!!this.hasLogin == false){
          this.$toast("您尚未登录")
          return;
        }

        this.$dialog.confirm({
          title: '您确定修改此用户吗?'
        }).then(() => {
          /* 显示遮罩层*/
          // this.showOverlay = true
          this.isDoing = true
          /* *****************************/
          let params = {
            userId : this.$route.query.userId,
            key : arg[0],
            value : arg[1]
          }
          /* 发送请求*/
          this.$api.user.setOtherTag(params).then(
              res =>{
                  this.isDoing = false
                  switch(res.data){
                    case 'no_power':
                    /* 隐藏遮罩层*/
                    // this.showOverlay = false
                          this.$toast("您的权限不足")
                        break;
                    default:
                    /* 隐藏遮罩层*/
                    // this.showOverlay = false
                      // this.setHandler([res.data.key,res.data.value])
                      this.msggg[res.data.key] = res.data.value
                      // console.log(this.msggg[res.data.key],'ggggggggg')
                      this.$toast("修改成功")
                     //default结束
                  }
              }
          )
          /* *****************************/
        }).catch(() => {
          // on cancel
        });
        /* *****************************/

        // let params = {
        //   userId : this.$route.query.userId,
        //   key : arg[0],
        //   value : arg[1]
        // }
        // /* 发送请求*/
        // this.$api.user.setOtherTag(params).then(
        //     res =>{
        //         switch(res.data){
        //           // case 'success':
        //           //       this.$toast("修改成功")
        //           //     break;
        //           default:
        //             // this.setHandler([res.data.key,res.data.value])
        //             this.msggg[res.data.key] = res.data.value
        //             // console.log(this.msggg[res.data.key],'ggggggggg')
        //             this.$toast("修改成功")
        //            //default结束
        //         }
        //     }
        // )
        /* */
      },
      // ...login.mapMutations({
      //       	setHandler: 'setHandler'
      //       }),
    },
    /* 计算*/
    computed:{
      isDoing:{
		        get () {
		              return this.$store._modulesNamespaceMap['win/'].context.state.isDoing
		        },
		        set (value) {
		          this.$store._modulesNamespaceMap['win/'].context.commit('isDoingHandler', value)
		        }
		      },
      ...login.mapState({
           	hasLogin : state => state.hasLogin
           }),
     // ...nav.mapState({
     //      	code : state => state.code
     //      })
    },
    // 组件
    components : {
    	/* 这个组件名字不能和原生标签名重复 */
      // SignUp
    },
    /* 挂载阶段*/
    mounted() {

    },
    // 监听
    watch:{
      // 'obj.a': {
      //     handler(newName, oldName) {
      //       console.log('obj.a changed');
      //     },
      //     immediate: true,
      //     // deep: true
      //   }
    }
  }
</script>

<style scoped lang="scss">
  .setOtherTag{
    .btn{
      margin-top: 20px;
      font-size: 40px;
      color:white;
      background-color:rgb(21,208,101);
      @include wh(750px,100px)
      @include flex(row,center,center)
      span{
        flex-grow:1;
        @include flex(row,center,center)
      }
    }
  }


</style>



