<template>
  <div class="otherUser am-otherUser">
    <!-- <SignUp></SignUp> -->
    <UserTag :msgg="userInfo" class="headPor"></UserTag>
    <!-- <HeadPor class="headPor"
    :msgg="{authen:userInfo.user_authen,img:userInfo.user_img,name:userInfo.user_name,user_lv:userInfo.user_lv,gold:userInfo.user_gold}"></HeadPor> -->
    <div class="folAndFans" >
    	<div class="fol" @click="folAndFans('follow')">关注{{userInfo.user_follow}}</div>
    	<div class="fans" @click="folAndFans('fans')">粉丝{{userInfo.user_fans}}</div>
    </div>
	<!-- 关注 -->
    <!-- <van-button type="primary" size="large" @click='follow'
    :loading='waiting_res_btnLoading' loading-type="spinner" loading-text="加载中...">
    {{followInfo}}</van-button> -->
    <div class="follow" @click='follow' v-show="!ws_follow">{{followInfo}}</div>
    <div class="follow" v-show="ws_follow">获取数据中...</div>

    <div class="cellGroup">
      <!-- 我的收藏 -->
      <div class="cell van-hairline--bottom" @click="toOther('article')">
        <!-- 左边 -->
        <div class="left">
          <i class="iconfont mymyarticle">&#xe603;</i>
          <span>TA的文章</span>
        </div>
        <!-- <i class="iconfont right">&#xe611;</i> -->
        <div class="rightTwo">
          <span>{{userInfo.user_article_count}}</span>
          <i class="iconfont right">&#xe611;</i>
        </div>
      </div>
      <!-- 我的收藏 -->
      <div class="cell van-hairline--bottom" @click="toOther('col')">
        <!-- 左边 -->
        <div class="left">
          <i class="iconfont col">&#xe68b;</i>
          <span>TA的收藏</span>
        </div>
        <!-- <i class="iconfont right">&#xe611;</i> -->
        <div class="rightTwo">
          <span>{{userInfo.user_col_count}}</span>
          <i class="iconfont right">&#xe611;</i>
        </div>
      </div>

      <SetOtherTag v-if='currentUserInfo.user_super_admin == 1' :msggg='userInfo'></SetOtherTag>

    </div>

  </div>
</template>

<script>
  /* 引入设置标签组件*/
  import SetOtherTag from "@/components/utils/SetOtherTag"
	/* 引入头像组件*/
	// import HeadPor from "@/components/utils/HeadPor"
  import UserTag from "@/components/utils/UserTag"

  import {createNamespacedHelpers} from 'vuex'
  const list=createNamespacedHelpers('list')
  const login=createNamespacedHelpers('login')
  // const navBar_Every=createNamespacedHelpers('navBar_Every')
  export default{
    name:"",
    activated(){
      if(this.firstEnter >1 && (this.fromPath != '/otherForAAndC'
      && this.fromPath != '/otherForFAndF'
      && this.fromPath != '/search')){
        this.isBuffering = true
        /* 如果不是在第一次进入并且...*/
        this.mountedFn()
      }else{
        this.firstEnter ++
      }
    },
    /* 数据*/
    data(){
      return {
        firstEnter : 0,
        userInfo :{},
        // waiting_res_btnLoading: true,
        ws_follow : true,
        followInfo : ''
      }
    },
    /* 方法*/
    methods:{
      mountedFn(){
        this.firstEnter ++
        // 缓冲显示
        this.isBuffering = true
        // this.titleHandler('ta的主页')
        var params ={
        	userId : this.$route.query.userId
        }
        // 其他用户的主页
        this.$api.user.otherUser(params).then(
            res =>{
              this.isBuffering = false
                switch(res.data){
                  case 'No_information':
                      this.$toast("没有此用户信息")
                    break;
                  default:
                    console.log(res.data,'其他用户数据')
                    this.userInfo = res.data
                   //default结束
                }
            }
        )
        // 登录用户是否关注了这个用户
        this.$api.user.getFollow(params).then(
            res =>{
              this.ws_follow = false
                switch(res.data){
                  case 'noFol':
                        this.followInfo = '未关注'
                      break;
                  case 'isFol':
                       this.followInfo = '已经关注'
                      break;
                  default:

                   //default结束
                }
            }
        )
      },
		folAndFans(params){
		  this.$router.push({
		    path:'/otherForFAndF',
		    query : {
		      folAndFans : params,
			  otherUserId : this.userInfo.user_id
		    }
		  })
		},
      // 导航栏标题
      // ...navBar_Every.mapMutations({
      //       	titleHandler: 'titleHandler'
      //       }),
      follow(){
        // console.log('事件')
        if(this.ws_follow == true){
           console.log('正在等待回应');
           return;
        }
        // console.log('请求')
        this.ws_follow = true
        this.isDoing = true
        var params ={
        	userId : this.$route.query.userId
        }
        this.$api.user.follow(params).then(
            res =>{
              this.isDoing = false
              this.ws_follow = false
                switch(res.data){
                  case 'noFol':
                        this.followInfo = '未关注'
                      break;
                  case 'isFol':
                       this.followInfo = '已经关注'
                      break;
                  default:

                   //default结束
                }
            }
        )
      },
	  toOther(params){
      this.$router.push({
        path:'/otherForAAndC',
        query : {
          articleAndCol : params,
          otherUserId : this.userInfo.user_id
        }
      })
	  }
      // toOther(params){
      //   var that = this
      //   switch(params){
      //     case 0:
      //       this.$router.push({
      //         path:'/otherArt',
      //         query : {
      //           userId : that.$route.query.userId
      //         }
      //       })
      //        break;
      //     case 1:
      //       this.$router.push({
      //         path:'/otherCol',
      //         query : {
      //           userId : that.$route.query.userId
      //         }
      //       })
      //        break;
      //   }

      // },
    },
    /* 计算*/
    computed:{
      isDoing:{
            get () {
                  return this.$store._modulesNamespaceMap['win/'].context.state.isDoing
            },
            set (value) {
              this.$store._modulesNamespaceMap['win/'].context.commit('isDoingHandler', value)
            }
          },
      ...login.mapState({
           	currentUserInfo : state => state.userInfo
           }),
      fromPath:{
        get () {
              return this.$store._modulesNamespaceMap['path/'].context.state.fromPath
        },
        set (value) {
          this.$store._modulesNamespaceMap['path/'].context.commit('fromPathHandler', value)
        }
      },
        // ...nav.mapState({
        //       code : state => state.code
        //     })
        isBuffering:{
         get () {
               return this.$store._modulesNamespaceMap['win/'].context.state.isBuffering
         },
         set (value) {
           this.$store._modulesNamespaceMap['win/'].context.commit('isBufferingHandler', value)
         }
        },
    },
    // 组件
    components : {
    	/* 这个组件名字不能和原生标签名重复 */
      // HeadPor,
      UserTag,
      SetOtherTag
    },
    /* 挂载阶段*/
    mounted() {
      this.mountedFn()
      // this.titleHandler('ta的主页')
      // var params ={
      // 	userId : this.$route.query.userId
      // }
      // // 其他用户的主页
      // this.$api.user.otherUser(params).then(
      //     res =>{
      //         switch(res.data){
      //           case 'No_information':
      //               this.$toast("没有此用户信息")
      //             break;
      //           default:
      //             console.log(res.data,'其他用户数据')
      //             this.userInfo = res.data
      //            //default结束
      //         }
      //     }
      // )
      // // 登录用户是否关注了这个用户
      // this.$api.user.getFollow(params).then(
      //     res =>{
      //       this.ws_follow = false
      //         switch(res.data){
      //           case 'noFol':
      //                 this.followInfo = '未关注'
      //               break;
      //           case 'isFol':
      //                this.followInfo = '已经关注'
      //               break;
      //           default:

      //            //default结束
      //         }
      //     }
      // )
    },
    // 监听
    watch:{
      // 'obj.a': {
      //     handler(newName, oldName) {
      //       console.log('obj.a changed');
      //     },
      //     immediate: true,
      //     // deep: true
      //   }
    }
  }
</script>

<style scoped lang="scss">
  .am-otherUser{
    // @extend %top;
  }
  .otherUser{
    @extend %paddingTop;
    .headPor{
      padding:0 25px;
    }
	.folAndFans{
	  @include flex(row,space-around ,center)
	  .fol,
	  .fans{
	    @include flex(row,space-around,center)
	    background-color:rgb(21,208,101);
	    // @include wh(200px,80px);
      height: 80px;
      padding:0 20px;
	    color:white;
	    border-radius:20px;
	  }
	}
    .follow{
      margin-top: 20px;
      font-size: 40px;
      color:white;
      background-color:rgb(21,208,101);
      @include wh(750px,100px)
      @include flex(row,center,center)

    }
    .cellGroup{

      .cell{
        // border-bottom: 1px solid #969799;
        padding: 0 25px;
        @include flex(row,space-between ,center)
        .left{
          @include wh(200px,100px)
          // background-color:red;
          @include flex(row,space-between ,center)
          span{
            font-size:34px;
            font-weight: 500;
            color: rgb(55,55,55);
          }
          .mymyarticle{
            font-size:30px;
            color: rgb(21,208,101);
          }
          // 我的收藏
          .col{
            font-size:30px;
            color: red;
          }
          // 修改资料
          .mo{
            font-size:30px;
            color: rgb(66,66,66);
          }
          //设置
          .set{
            font-size:30px;
            color: rgb(75,175,227);
          }
        }
        .right{
          font-size:30px;
          color: rgb(224,224,224);
          font-weight: 600;
        }
        .rightTwo{
          @include wh(100px,100px)
          @include flex(row,space-between ,center)
          span{
            color:rgb(238,188,20);
          }
        }
      }

    }
  }

</style>



